import React, { useState } from "react";
import { skillsList, Skills } from "./skills.js";
import "../index.css";
import { motion, AnimatePresence } from "framer-motion";

// ("On-going", "Healthcare Consulting Website Design", logo, [images], "Figma", (), figmaIcon , "ViewDetails", (), viewIcon)
export default function DProjectCardLarge({
  subheading,
  projectName,
  projectLogo,
  projectImages,
  button1Text,
  button1Color,
  button1Action,
  button1Icon,
  button2Text,
  button2Action,
  button2Icon,
  description,
  status,
}) {
  const [isDescriptionVisible, setDescriptionVisible] = useState(false);

  const toggleDescription = () => setDescriptionVisible(!isDescriptionVisible);

  return (
    <div className="relative flex w-full flex-col">
      <div className="relative w-full text-center flex justify-center text-stone-300 text-7xl font-black font-['Montserrat'] leading-10 z-0">
        {subheading}
      </div>
      <div className="relative w-full flex flex-col bg-white shadow-2xl z-10 rounded-md">
        <div className="w-full h-20  flex flex-row items-center p-2 gap-2">
          <div className="w-16 h-16 shrink-0 rounded-full overflow-hidden">
            <img src={projectLogo} className="w-16 h-16" alt="" />
          </div>
          <div className="flex flex-shrink">
            <div className="grow shrink basis-0 self-stretch text-black text-xl lg:text-2xl font-semibold font-['Montserrat']">
              {projectName}
            </div>
          </div>
        </div>
        <div className="w-full h-4/6 bg-black overflow-x-auto overflow-y-clip flex flex-row items-center gap-2">
          {projectImages.map((image, index) => (
            <div key={index} className="h-[255px] shrink-0 bg-slate-100">
              <img
                src={image}
                alt={`Project detail ${index + 1}`}
                className="w-full h-full object-cover"
              />
            </div>
          ))}
        </div>
        <AnimatePresence>
          {isDescriptionVisible && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: "auto" }}
              exit={{ opacity: 0, height: 0 }}
              transition={{ duration: 0.2 }}
              className="description w-full p-6 pt-2 pb-0 overflow-hidden"
            >
              <p>
                {description}
                <br />
                <br />
                <b>Status</b>: {status}
              </p>
            </motion.div>
          )}
        </AnimatePresence>
        <div className="w-full h-1/6  flex flex-row items-center gap-2 p-2 pt-3 pb-3 rounded-bl-md rounded-br-md">
          {/* Button 1 with SVG */}
          <button
            onClick={button1Action}
            className={`w-full h-full ${button1Color} flex flex-row items-center justify-center gap-0 rounded-md`}
          >
            <span className="text-center text-white text-lg md:text-xl font-medium font-['Montserrat'] leading-loose">
              {button1Text}
            </span>
            <div className="w-9 h-9 shrink-0 flex rounded-full overflow-hidden items-center justify-center">
              {/* Use img tag for Button 1 SVG */}
              <img src={button1Icon} className="w-7 h-7" alt="Button 1 icon" />
            </div>
          </button>
          <button
            onClick={toggleDescription}
            className="w-full h-full bg-black flex flex-row items-center justify-center gap-0 rounded-md"
          >
            <span className="text-center text-white text-lg md:text-xl font-medium font-['Montserrat'] leading-loose">
              {button2Text}
            </span>
            <div className="w-9 h-9 shrink-0 flex rounded-full overflow-hidden items-center justify-center">
              {/* Use img tag for Button 2 SVG */}
              <img src={button2Icon} className="w-7 h-7" alt="Button 2 icon" />
            </div>
          </button>
        </div>
      </div>
    </div>
  );
}

