import { Projects } from "./components/Projects";
import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import "./App.css";
import "./index.css";

function App() {
  const [isExpanded, setIsExpanded] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };

    // Check on initial render
    checkMobile();

    // Add event listener for window resize
    window.addEventListener("resize", checkMobile);

    // Cleanup event listener
    return () => window.removeEventListener("resize", checkMobile);
  }, []);

  // const [isProjects, setIsProjects] = useState(false);
  // const [isAboutme, setAboutme] = useState(false);
  // const [isResume, setIsResume] = useState(false);
  // const [isContact, setIsContact] = useState(false);

  // Function to toggle navbar expansion
  const toggleNavbar = () => {
    setIsExpanded(!isExpanded);
  };

  // Variants for the navbar animation
  const navbarVariants = {
    initial: { y: "100%" }, // Positioned at the bottom of the first page
    animate: { y: isExpanded ? "15%" : "15%" }, // Move up to cover the screen when expanded
  };

  const tabsVariants = {
    initial: { opacity: "100%" },
    animate: { opacity: isExpanded & !isMobile ? "0%" : "100%" },
  };

  return (
    <div className="App">
      <header className="App-header">
        {/* Content before the navbar */}
        <div className="profile-container">
          <img
            src="/assets/profile.jpg"
            className="profile-image"
            alt="Gabriel Zafra"
          />
        </div>
        <h1>Hi, I'm </h1>
        <h2>Gabriel</h2>
        <h2>Zafra</h2>
        <p className="tagline">
          a <span className="bold-text">UX Designer</span>,
          <br />
          passionate about
          <br />
          <span className="bold-text">Interaction Design</span>
          <br />
          and
          <br />
          <span className="bold-text">Visual Design</span>
        </p>
      </header>
      <motion.div
        className="navbar"
        variants={navbarVariants}
        initial="initial"
        animate="animate"
        transition={{ type: "tween", duration: 0.5 }}
        onClick={null}
        // put back toggle here
      >
        <motion.div
          className="tabs pt-4 shrink-0"
          variants={tabsVariants}
          initial="initial"
          animate="animate"
          transition={{ type: "tween", duration: 0.5 }}
        >
          <h3 className="projects shrink-0">Projects</h3>
          <h3>•</h3>
          <h3 className="about-me shrink-0">About me</h3>
          <h3>•</h3>
          <h3 className="resume shrink-0">Resume</h3>
          <h3>•</h3>
          <h3 className="contact shrink-0">Contact</h3>
        </motion.div>

        <div className="main w-full h-full flex flex-row grow-0 shrink-0 justify-evenly">
          <div className="h-full scroll-to gap-20 text-center justify-start pt-24 hidden  md:flex md:flex-col ">
            <h4 className="projects-big">Projects</h4>
            <h4 className="about-me-big">About me</h4>
            <h4 className="resume-big">Resume</h4>
            <h4 className="contact-big">Contact</h4>
          </div>

          <div className="w-full md:w-[57%] h-full relative overflow-y-auto overflow-x-clip hide-scrollbar">
            <Projects />
            <div className="h-1/2 w-3 bg-white"></div>
          </div>
        </div>
      </motion.div>
    </div>
  );
}

export default App;
