export const skillsList = {
  Design: [
    "User-centered Design",
    "Interaction Design",
    "Adobe XD",
    "User Research",
    "Usability Testing",
    "Blender",
    "Wireframing",
    "User Personas",
    "Adobe Photoshop",
    "Prototyping",
    "Journey Mapping",
    "Adobe Illustrator",
    "Responsive Design",
    "Figma",
  ],
  Code: [
    "React.js",
    "Tailwind CSS",
    "Dart via Flutter",
    "HTML, CSS, JavaScript",
    "Swift",
    "Lua",
    "Python",
    "Git & Version Control",
    "API Integration",
    "Java",
    "C# (Unity)",
  ],
};

export function Skills({ category, skills }) {
  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4 p-4">
      {Object.entries(skills).map(([category, skillsList]) => (
        <div key={category} className="flex flex-col">
          <h3 className="text-xl font-bold mb-2">{category}</h3>
          <ul className="list-none m-0 p-0 space-y-1">
            {skillsList.map((skill, index) => (
              <li key={index} className="truncate whitespace-nowrap">{skill}</li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  );
}
