export function Projects() {
  return (
    <div className="items-center p-6 pt-5 flex flex-col gap-y-16">
      {projectList.map((project, index) => {
        // Conditionally render ProjectCardSmall or ProjectCardLarge based on cardSize
        const CardComponent =
          project.cardSize === "large" ? DProjectCardLarge : ProjectCardSmall;

        return (
          <CardComponent
            key={index}
            subheading={project.subheading}
            projectName={project.projectName}
            projectLogo={project.projectLogo}
            projectImages={project.projectImages}
            button1Text={project.button1Text}
            button1Icon={project.button1Icon}
            button1Action={project.button1Action}
            button1Color={project.button1Color}
            button2Text={project.button2Text}
            button2Icon={project.button2Icon}
            button2Action={project.button2Action}
            description={project.description}
            status={project.status}
          />
        );
      })}
      <BioCard />
      <EducationCard />
      <ExperienceCard />
      <SkillsCard />
      <ResumeCard />
      <ContactCard />
    </div>
  );
}
